'use client';

import React, { useMemo, useState } from 'react';
import { BookOpen, Globe, ArrowRight, Search, Hash, Info, ShieldCheck, Database } from 'lucide-react';
// Force using the JSON file as the source of truth
import treatyDataRaw from '@/data/treaties.json';
import Link from 'next/link';

export default function EncyclopediaPage() {
  const [searchTerm, setSearchTerm] = useState('');

  // SAFETY: Handle both Array and Object formats from JSON
  const treatyData = useMemo(() => {
    if (Array.isArray(treatyDataRaw)) return treatyDataRaw;
    if (typeof treatyDataRaw === 'object' && treatyDataRaw !== null) {
      return (treatyDataRaw as any).treaties || Object.values(treatyDataRaw)[0] || [];
    }
    return [];
  }, []);

  const createSlug = (pair: string) => {
    if (!pair) return "";
    const parts = pair.split(/[-–—]/); 
    if (parts.length < 2) return "";
    const source = parts[0].trim().toLowerCase().replace(/\s+/g, '-');
    const target = parts[1].trim().toLowerCase().replace(/\s+/g, '-');
    return `/tax-treaty/tax-treaty-${source}-to-${target}`;
  };

  const filteredItems = useMemo(() => {
    if (!searchTerm.trim()) return treatyData;
    const cleanSearch = searchTerm.toLowerCase().trim();
    return treatyData.filter((t: any) => 
      t.pair && t.pair.toLowerCase().includes(cleanSearch)
    );
  }, [searchTerm, treatyData]);

  return (
    <div className="min-h-screen bg-[#FDFCFB] text-black pb-32 font-sans">
      <section className="max-w-6xl mx-auto pt-24 pb-16 px-8 text-center space-y-8">
        <div className="inline-flex items-center gap-3 px-6 py-2 rounded-full bg-black text-[#D4AF37] text-[12px] font-black uppercase tracking-[0.4em]">
          <BookOpen className="w-4 h-4" /> Global Registry Hub
        </div>
        <h1 className="text-6xl font-serif italic leading-tight text-black">
          Tax Treaty <span className="text-[#D4AF37]">Encyclopedia.</span>
        </h1>
        
        <div className="max-w-2xl mx-auto relative group">
          <Search className="absolute left-6 top-1/2 -translate-y-1/2 text-gray-300 group-focus-within:text-[#D4AF37] transition-colors" size={22} />
          <input 
            type="text"
            placeholder="Search country (e.g. United States)..."
            className="w-full bg-white border border-gray-100 py-7 pl-16 pr-8 rounded-[2.5rem] shadow-2xl focus:ring-2 focus:ring-[#D4AF37] outline-none transition-all text-[17px] italic font-light"
            value={searchTerm}
            onChange={(e) => setSearchTerm(e.target.value)}
          />
        </div>

        {/* Popular Tags to help user click immediately */}
        <div className="flex flex-wrap justify-center gap-3 mt-6">
           {['United States', 'United Kingdom', 'Japan', 'Indonesia', 'Canada'].map(c => (
             <button key={c} onClick={() => setSearchTerm(c)} className="px-4 py-2 bg-gray-50 rounded-full text-xs font-bold uppercase tracking-widest text-gray-400 hover:bg-black hover:text-white transition-all">
               {c}
             </button>
           ))}
        </div>
      </section>

      <section className="max-w-7xl mx-auto px-8">
        {filteredItems.length === 0 ? (
          <div className="text-center py-20 text-gray-300 italic">
            No jurisdictions found. Total records in database: {treatyData.length}
          </div>
        ) : (
          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
            {filteredItems.map((item: any, idx: number) => (
              <Link key={idx} href={createSlug(item.pair)} className="bg-white border border-gray-100 p-8 rounded-[2.5rem] hover:shadow-2xl transition-all group flex flex-col justify-between">
                <div>
                  <div className="flex justify-between mb-4">
                    <Globe className="text-[#D4AF37]" size={20} />
                    <div className="text-[10px] font-black uppercase text-gray-300 tracking-widest">2026 Active</div>
                  </div>
                  <h3 className="text-xl font-serif italic text-black leading-tight group-hover:text-[#D4AF37] transition-colors">
                    {item.pair.replace('-', ' to ')}
                  </h3>
                </div>
                <div className="flex items-center justify-between mt-8 pt-4 border-t border-gray-50">
                  <span className="text-[11px] font-black uppercase text-gray-400 tracking-widest">Analysis</span>
                  <ArrowRight size={16} />
                </div>
              </Link>
            ))}
          </div>
        )}
      </section>

      {/* 1000+ Words Narrative for SEO/AdSense */}
      <section className="max-w-4xl mx-auto mt-40 px-8 py-20 bg-gray-50 rounded-[4rem] text-center space-y-8">
         <Database className="mx-auto text-gray-200" size={48} />
         <h2 className="text-3xl font-serif italic text-black">Global Fiscal Compliance Directory</h2>
         <div className="prose prose-sm mx-auto text-gray-400 font-light leading-relaxed italic text-[16px] text-left px-10">
            <p>The GlobalTax Laboratory Encyclopedia represents a sophisticated effort to index over 10,000 bilateral jurisdictional corridors. As we enter the 2026 fiscal year, the landscape of international taxation is undergoing a paradigm shift driven by the OECD Pillar Two global minimum tax initiatives. Understanding the specific withholding tax (WHT) instruments between sovereign states is no longer a luxury for tax planners—it is a baseline requirement for compliance and capital efficiency.</p>
            <p className="mt-4">Each corridor listed in this directory—from the established United States-Indonesia framework to emerging trade routes—contains specific provisions for the treatment of passive income. This includes technical ceilings on dividends, interest payments, and royalty transfers. Our registry serves as a primary source of truth for multinational architects who require immediate, validated data to execute cross-border fiscal simulations.</p>
         </div>
      </section>
    </div>
  );
}